<template>
  <div class="item_list">

    <form action="/search">
      <van-search
        placeholder="请输入店铺名称"
        v-model="searchVal"
        @search="initData"
        @cancel="onCancel"
        showAction/>
    </form>

    <van-list
      v-model="loading"
      :finished="finished"
      :immediate-check="false"
      :offset="100"
    >
      <van-card
        v-for="(item, i) in items"
        :key="i"
        :title="item.shopName"
        :thumb="item.shopLogo">
        <div slot="footer" class="order_list--footer_btn">
          <van-button size="small" @click="selectShop(item.shopId, item.shopName)">选择店铺</van-button>
        </div>
      </van-card>
    </van-list>

    <is-empty v-if="isEmpty">暂无店铺</is-empty>

  </div>
</template>

<script>
  import { SHOPS } from "@/api/user";
  import IsEmpty from "@/vue/components/is-empty/";
  import { Search, Card, Toast } from "vant";

  // import loadMore from "@/vue/mixin/list-load-more";

  export default {
    name: "shops",

    // mixins: [loadMore],

    data() {
      return {
        searchVal: "",
        finished : false,
        loading : false,
        isEmpty : false,
        items:[]
      };
    },

    created() {
    },

    methods: {
      initData(loadMore = false) {
        if(!this.searchVal){
          Toast.fail("请输入店铺名称");
          return;
        }
        this.loading = true;
        return this.$reqGet(SHOPS, {
          searchKey: this.searchVal
        },{
          hideLoading: true
        }).then(res => {
          return this.loadSuccess(res);
        }).catch((err) => {
          return this.loadSuccess(err);
        });
      },

      loadSuccess(res){
        if(!res.data || res.data.code != 0){
          let msg = (res && res.msg) ? res.msg : "获取好友列表失败";
          Toast.fail(msg);
          return;
        }
        let list = res.data.data;
        if(list && list.length > 0){
          this.items.push(...list);
        }else{
          this.isEmpty = true;
        }
        this.loading = false;
        this.finished = true;
      },

      onCancel() {
        this.$router.go(-1);
      },

      selectShop(id, name){
        this.$bus.$emit('selected',id, name);
        this.$router.go(-1);
      }
    },

    components: {
      [Card.name]: Card,
      [Search.name]: Search,
      [IsEmpty.name]: IsEmpty,
      [Toast.name]: Toast
    }
  };
</script>

<style lang="scss" scoped>


  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: all .5s;
  }

  .item_list {
    background-color: #fff;
    padding-bottom: 0;
  }

  .fixedTop {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 999;
  }

  .items_loading {
    margin: 0 auto;
  }

  .filterItem {
    width: 40%;
    height: 100%;
    li {
      padding: 10px;
      &.filter_active {
        color: $red;
      }
    }
  }

  .backTop {
    position: fixed;
    right: 20px;
    bottom: 20px;
    font-size: 24px;
  }
</style>



